  checkInternet = () =>
  {
	let status = this.state.status;
	status = window.navigator.onLine ? 'online' : 'offline';
	this.setState({ status: status });
	if (status === 'offline')
	{
		this.setState({ activePanel: 'off', activeStory: 'off' })
	}
	else if (status === 'online')
	{
		if (this.state.activePanel === 'off')
		{
			//тут перемещение на главную страницу
			this.setState({ activePanel: 'main', activeStory: 'main' })
		}
	}

  }

  getHash(str, algo = "SHA-256")
  {
	  let strBuf = new TextEncoder('utf-8').encode(str);
	  return crypto.subtle.digest(algo, strBuf)
	    .then(hash => {
	      window.hash = hash;
	      // here hash is an arrayBuffer,
	      // so we'll connvert it to its hex version
	      let result = '';
	      const view = new DataView(hash);
	      for (let i = 0; i < hash.byteLength; i += 4) {
	        result += ('00000000' + view.getUint32(i).toString(16)).slice(-8);
	      }
	      return result;
	    });
	}




  handleChangeOferta(event)
  {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;
    if (value==true)	this.setState({ allowofertahidden: '1' });
    	else this.setState({ allowofertahidden: '0' });

    this.setState({
      [name]: value
    });
  }
  handleChangefetchedUser(event)
  {
  	var fio = event.target.value;
  	fio = fio.replace(/\s+/g, ' ');
  	fio = fio.replace('--', '-');
  	fio = fio.replace(' - -', ' -');
  	fio = fio.replace('- - ', '- ');
  	fio = fio.replace(/[^А-Яа-яЁёA-Za-z -]/g, '');


  	if (event.target.value.length<=25) this.setState({ fetchedUser: fio });
  }

