onStoryChange (e)
	{


		this.setState({ activeStory: e.currentTarget.dataset.story });
		if (e.currentTarget.dataset.story!="order") window.scrollTo(0, 0)
		if (e.currentTarget.dataset.story=="lk ") window.scrollTo(0, 0);
		//alert(e.currentTarget.dataset.story);
		if (this.state.activeStory==="order")
		{
			if (e.currentTarget.dataset.story==="lk")
			{
				window.scrollTo(0, 0);
			}

		}

		if (this.state.activeStory==="main" || this.state.activeStory==="lk" || this.state.activeStory==="contacts")
		{
			if (e.currentTarget.dataset.story==="order")
			{
				window.scrollTo(0, 0);
			}

		}


		if (this.state.activeStory==="oferta")
		{
			if (e.currentTarget.dataset.story==="order")
			{
				window.scrollTo(300, 300);
			}
		}

		if (this.state.activeStory==="pricelist")
		{
			if (e.currentTarget.dataset.story==="order")
			{
				window.scrollTo(0, 0);
			}
		}
	}




